/* Header/Menu */

.menu {
    max-width: 1140px;
    margin: 0 auto;
    padding: 15px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.menu a {
    color: #2C223A;
}

.menu-nav ul {
    display: flex;
    flex-wrap: wrap;
}

.menu-nav a {
    font-size: 1.125em;
    display: block;
    padding: 10px 0 0 20px;
}

.menu-nav a:hover {
    color: rgba(240, 103, 23, 0.74);
}


@media(max-width: 600px) {

    .menu-nav a {
        font-size: 1.125em;
        display: block;
        padding-top: 20%;
        padding-right: 0px;
    }
}