@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;610;700;800&display=swap);
:root {
    --font-fam: 'Manrope', Arial, Helvetica, sans-serif;
    --orange-h1: #F06517;
    --gray-p: #5A5266;
}

/* introduction  */

.introduction-container {
    max-width: 1140px;
    margin: 130px auto;
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;
}

/* text description */

.intro-description {
    flex: 2 1 600px;
}

.intro-description h1 {
    font-family: 'Manrope', Arial, Helvetica, sans-serif;
    font-family: var(--font-fam);
    font-size: 4.5em;
    font-weight: 800;
    color: #F06517;
    color: var(--orange-h1);
    text-shadow: -3px 2.5px 0px rgba(44, 34, 58, 0.125);
    margin-bottom: 8px;
}


.intro-description p {
    font-family: 'Manrope';
    font-size: 1.5em;
    font-weight: 400;
    color: #5A5266;
    color: var(--gray-p);
    line-height: 125%;
}

.intro-description .orange-text {
    font-family: 'Manrope';
    color: #F06517;
    color: var(--orange-h1);
    font-weight: 700;
    padding-bottom: 25px;
}

/* social buttons */

.intro-social {
    display: flex;
    flex-flow: column wrap;
    align-items: flex-end;
}

.social-item {
    margin: 12px 0px;
}

/* mobile */

@media(max-width: 600px) {
    .intro-social {
        display: flex;
        flex-flow: row wrap;
        align-items: flex-start;
    }
    .social-item {
        margin: 30px 12px 0 0;
    }
    .introduction-container {
        margin: 15px auto;
    }
    .intro-description h1 {
        font-size: 3.5em;
    }
    
}


/* About */

.about-container {
    max-width: 1140px;
    margin: 130px auto;
    padding: 15px 0;
    display: flex;
    align-items: flex-start;
    flex-flow: column wrap;
}

.about-content {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 56px;
}

.about-text {
    flex: 2 1 260px;
    margin-left: 100px;
    margin-top: 30px;
}

.about-text p {
    font-size: 1.31em;
    font-weight: 500;
    line-height: 34px;
    color: #443D4C;
    padding-bottom: 34px;
}

.about-text a {
    font-size: 1.12em;
    font-weight: 400;
    color: #F06517;
    align-items: center;
    padding-bottom: 24px;
}
/* footer */
.footer-container {
    max-width: 1140px;
    margin: 130px auto 0 auto;
    padding: 15px 0;
    display: flex;
    flex-flow: column wrap;
}
.footer-text {
    display: flex;
    flex-flow: column wrap;
    align-items: flex-end;
}
.footer-text h1 {
    font-size: 4.5em;
    font-weight: 800;
    color: #2D2933;
    text-shadow: -4.5px 5px 0px rgba(250, 101, 2, 0.125);
}

.footer-text h2 {
    font-size: 2em;
    font-weight: 400;
    color: #2D2933;
    margin-bottom: 45px;
}

.footer-text a {
    font-size: 1.5em;
    color: #2C223A;

}

.footer-social {
    margin-bottom: 150px;
    display: flex;
    flex-flow: row wrap;
    align-items: flex-end;
    justify-content: space-between;
}

.social-icons {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-end;
    justify-content: space-between;
}

.social-icons img {
    padding-left: 25px;
}

.copyright {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 15px;
}

/* projects section */

.projects-container {
    max-width: 1140px;
    margin: 130px auto;
    padding: 15px 0;
    display: flex;
    align-items: flex-start;
    flex-flow: column wrap;
}

.project-item {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 56px;
}

.item-desc-text {
    margin-top: 35px;
    margin-left: 30px;
}

.item-desc-text h2 {
    font-size: 2.5em;
    color: #403154;
    margin-bottom: 16px;
}

.item-desc-text p{
    display: flex;
    font-size: 1.31em;
    color: #7D7685;
    align-items: center;
    padding-bottom: 24px;
}

.orange-link {
    font-size: 1.31em;
    font-weight: 700;
    color: #F06517;
    align-items: center;
    padding-bottom: 24px;
}

.separator::before {
    content: '';
    display: block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #F06517;
    margin-left: 8px;
    margin-right: 8px;
}

@media(max-width: 600px) {
    .projects-container {
        margin: 30px auto;
    }
    .item-desc-text {
        margin-top: 15px;
        margin-left: 0;
    }
    
    .item-desc-text h2 {
        margin-bottom: 3px;
    }

    .item-desc-text p{
        padding-bottom: 6px;
    }

    
}

*{
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html {
  overflow-x:hidden;
}

body {
  font-family: 'Manrope', sans-serif;
  background: #fafafa;
  padding: 0 15px;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;

}

img {
  display: block;
  max-width: 100%;
}

.root {
  padding: 0%;
  margin: 0%;
}

.section-header h1 {
  font-size: 3.5em;
  font-weight: 800;
  color: #2D2933;
  text-shadow: -4.5px 5px 0px rgba(250, 101, 2, 0.125);
  margin-bottom: 45px;
  
}

.bg-purple {
  position: absolute;
  left: 55%;
  margin-top: 230px;
}

.bg-orange {
  position: absolute;
  right: 60%;
  margin-top: 5%;
}

@media(max-width: 600px) {
  .bg-orange, .bg-purple {
    display: none;
  }
}
/* Header/Menu */

.menu {
    max-width: 1140px;
    margin: 0 auto;
    padding: 15px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.menu a {
    color: #2C223A;
}

.menu-nav ul {
    display: flex;
    flex-wrap: wrap;
}

.menu-nav a {
    font-size: 1.125em;
    display: block;
    padding: 10px 0 0 20px;
}

.menu-nav a:hover {
    color: rgba(240, 103, 23, 0.74);
}


@media(max-width: 600px) {

    .menu-nav a {
        font-size: 1.125em;
        display: block;
        padding-top: 20%;
        padding-right: 0px;
    }
}
